<!--  -->
<template>
  <el-dialog width="420px" class="front-dialog" :title="dialog.title" :close-on-click-modal="false" center :append-to-body="true" v-dialog-drag :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules">
      <div class="front-input">
        <span class="fa fa-user-o" aria-hidden="true"></span>
        <el-input v-model="sysUser.loginName" placeholder=""></el-input>
      </div>
      <div class="front-input">
        <span class="fa fa-key" aria-hidden="true"></span>
        <el-input v-model="sysUser.password" type="password" placeholder=""></el-input>
      </div>
      <div class="front-input">
        <span class="fa fa-key" aria-hidden="true"></span>
        <el-input v-model="sysUser.prePassword" type="password" placeholder=""></el-input>
      </div>
    </el-form>
    <div class="dialog-footer">
      <el-button class="sub-btn" @click="register"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {mapActions} from "vuex";
  import axios from "axios";
  import md5 from 'js-md5';
  export default {
    name: "sysUserForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          prePassword:"",
          password: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    mounted(){
    },
    methods: {
      ...mapActions(['commitFrontUserAuthor']),
      //
      open(option) {
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      getImage() {
        return this.$request.doGet('http://api.btstu.cn/sjtx/api.php?lx=c1&format=json').then(res => {
          return res.imgurl;
        })
      },
      //
      getUserAuthor() {
        const that = this;
        return that.$jsonp('https://api.map.baidu.com/location/ip?ak=k7ILtYBEvwy3piyGcdWnwMtAqDOLKTMs').then((addr) => {
          return that.getImage().then(avatarUrl=>{
            const country = "";//
            const province = addr.content.address_detail.province;//
            const city = addr.content.address_detail.city;//
            const openid = md5(avatarUrl+country+province+city);//
            return that.commitFrontUserAuthor({country: country, province: province, city: city,avatarUrl: avatarUrl,openid:openid}).then(res => {
              return res
            })
          });
        })
      },
      //
      register: function () {
        const that = this;
        if (that.sysUser.password == "") {
          this.showWarn("!");
          return false
        }
        if (that.sysUser.prePassword == "") {
          this.showWarn("!");
          return false
        }
        if (that.sysUser.password != that.sysUser.prePassword) {
          this.showWarn("!");
          return false
        }
        that.getUserAuthor().then(res=>{
          that.$request.doPost('/api/login/register', that.sysUser).then(res => {
            if (res.isOk) {
              that.dialog.open = false;
              that.showSuccess("");
              that.$emit('reloadList');
            } else {
              that.showWarn(res.info);
            }
          });
        })
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>
<style scoped>
  .front-dialog /deep/ .el-dialog__headerbtn .el-dialog__close{
    color: white!important;
  }
  .front-dialog /deep/ .el-dialog__header  {
    background: rgba(76,135,188,0.91)!important;
  }
  .front-dialog /deep/ .el-dialog__title  {
    font-weight: bold;
    color: white!important;
  }

  .sub-btn  {
    margin: 20px 0 10px;
    width: 100%;
    background: rgba(76, 135, 188, 0.91);
    color: white;
    height: 45px
  }
  .front-input {
    margin: 20px 0;
    padding: 3px 15px;
    border: 1px solid #eee;
    background:white;
    border-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .front-input span   {
    font-size: 15px;
    color: #464646;
    margin-right: 10px;
  }
</style>



